import axios from 'axios';
import ngeohash from 'ngeohash';
import { useEffect, useRef, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Row from 'react-bootstrap/Row';
import Spinner from 'react-bootstrap/Spinner';
import CardProduto from './componentes/CardProduto';
import ModalEndereco from './componentes/ModalEndereco';
import ModalHistorico from './componentes/ModalHistorico';
import usePersistedState from './hooks/usePersistedState';

import './App.css';

const TAMANHO_MAXIMO = 150;

const App = () => {
    const [periodo, setPeriodo] = useState("3");
    const [ordem, setOrdem] = useState("0");
    const [pesquisarPor, setPesquisarPor] = useState("NOME");
    const [termo, setTermo] = useState("");
    const [categoria, setCategoria] = useState("");
    const [distancia, setDistancia] = useState("20");
    const [categorias, setCategorias] = useState([]);
    const [produtos, setProdutos] = useState([]);
    const [loading, setLoading] = useState(false);
    const [fazerPesquisa, setFazerPesquisa] = useState(false);
    const [modalVisivel, setModalVisivel] = useState(false);
    const [modalHistoricoVisivel, setModalHistoricoVisivel] = useState(false);
    const [endereco, setEndereco] = usePersistedState("endereco", null);
    const [historicos, setHistoricos] = usePersistedState("historicos", []);
    const refTermo = useRef(null);

    useEffect(() => {
        if (refTermo.current)
            refTermo.current.focus();
        if (!endereco)
            setModalVisivel(true);
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        if (fazerPesquisa) {
            pesquisar();
            setFazerPesquisa(false);
        }
        // eslint-disable-next-line
    }, [fazerPesquisa]);

    const buscarCategorias = async () => {
        try {
            const response = await axios.get(
                `https://menorpreco.notaparana.pr.gov.br/api/v1/categorias` +
                `?local=${endereco.geohash}` +
                `&termo=${termo}` +
                `&raio=${distancia}`
            );
            const categoriasRetornadas = response.data.categorias;
            console.log('Categorias retornadas:', categoriasRetornadas);

            if (categoriasRetornadas.length === 0)
                return "";

            setCategorias(categoriasRetornadas);
            setCategoria(categoriasRetornadas[0].id);
            return categoriasRetornadas[0].id;
        } catch (error) {
            console.error('Erro ao buscar categorias:', error);
        }
    }

    const pesquisar = async () => {
        if (!termo) return; // Não faz a busca se o termo estiver vazio
        let todosProdutos = [];
        let offset = 0;
        let continuarBuscando = true;
        let categoriaAtual = categoria;
        let campoPesquisa = pesquisarPor === "NOME" ? "termo" : "gtin";

        adicionarHistorico(pesquisarPor, termo);

        try {
            setLoading(true);

            if (pesquisarPor === "NOME" && categorias.length === 0)
                categoriaAtual = await buscarCategorias();
            console.log('Categoria atual:', categoriaAtual);

            while (continuarBuscando && offset < TAMANHO_MAXIMO) {
                const response = await axios.get(
                    `https://menorpreco.notaparana.pr.gov.br/api/v1/produtos` +
                    `?local=${endereco.geohash}` +
                    `&ordem=${ordem}` +
                    `&data=${periodo}` +
                    `&${campoPesquisa}=${termo}` +
                    `&offset=${offset}` +
                    `&raio=${distancia}` +
                    (pesquisarPor === "NOME" ? `&categoria=${categoriaAtual}` : "")
                );

                const produtosRetornados = response.data.produtos;

                if (produtosRetornados.length > 0) {
                    todosProdutos = [...todosProdutos, ...produtosRetornados];
                    offset += 50; // Incrementa o offset para a próxima página
                } else {
                    continuarBuscando = false; // Para a busca se a lista de produtos estiver vazia
                }
            }
            setProdutos(todosProdutos);
            console.log('Todos produtos:', todosProdutos);
        } catch (error) {
            console.error('Erro ao buscar produtos:', error);
        } finally {
            setLoading(false);
        }
    };

    const pesquisarPorCodigo = async (codigo) => {
        setPesquisarPor("CODIGO");
        setTermo(codigo);
        setCategoria("");
        setCategorias([]);
        setFazerPesquisa(true);
    }

    const handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            pesquisar();
        } else if (/^[a-zA-Z0-9]$/.test(event.key)) { // Se for letra ou número
            setCategoria("");
            setCategorias([]);
        }
    };

    const selecionouEndereco = (endereco) => {
        endereco.geohash = ngeohash.encode(endereco.latitude, endereco.longitude, 12);
        console.log('endereco:', endereco);
        setEndereco(endereco);
        setModalVisivel(false);
    }

    const adicionarHistorico = (tipoPesquisa, termoPesquisa) => {
        const obj = { tipo: tipoPesquisa, termo: termoPesquisa, data: new Date() };
        if (historicos.length === 0) {
            setHistoricos([obj]);
        } else {
            const historicoFiltrado = historicos.filter(h => h.termo !== termoPesquisa);
            setHistoricos([obj, ...historicoFiltrado]);
        }
    }

    const pesquisarHistorico = (historico) => {
        setPesquisarPor(historico.tipo);
        setTermo(historico.termo);
        setCategoria("");
        setCategorias([]);
        setFazerPesquisa(true);
        setModalHistoricoVisivel(false);
    }

    return (<>
        {/* Topo */}
        <Navbar bg="body-secondary" className="mb-3">
            <Container>
                <Navbar.Brand>Menor Preço</Navbar.Brand>
                <Nav className="me-auto">
                    <NavDropdown title="Menu">
                        <NavDropdown.Item onClick={() => setModalVisivel(true)} className="text-wrap">{endereco?.nome}</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item onClick={() => setModalHistoricoVisivel(true)}>Histórico</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.ItemText className="bg-body-secondary">Links úteis</NavDropdown.ItemText>
                        <NavDropdown.Item href="https://menorpreco.notaparana.pr.gov.br/" target="_blank" rel="noreferrer">Menor Preço</NavDropdown.Item>
                        <NavDropdown.Item href="https://compras.menorpreco.pr.gov.br" target="_blank" rel="noreferrer">Compras Menor Preço</NavDropdown.Item>
                    </NavDropdown>
                </Nav>
            </Container>
        </Navbar>
        {/* .Topo */}

        {/* Meio */}
        <Container>
            {/* Filtros */}
            <div className="bg-body-tertiary rounded-3 p-3">
                <Row className="justify-content-center mb-md-2">
                    <Col md="3" xl="2">
                        <Form.Label>Pesquisar por:</Form.Label>
                        <Form.Select
                            value={pesquisarPor}
                            onChange={(e) => setPesquisarPor(e.target.value)}
                        >
                            <option value="NOME">Nome</option>
                            <option value="CODIGO">Código</option>
                        </Form.Select>
                    </Col>
                    <Col md="5" xl="4">
                        <Form.Label>Produto:</Form.Label>
                        <Form.Control
                            value={termo}
                            onChange={(e) => setTermo(e.target.value)}
                            onKeyDown={handleKeyPress}
                            placeholder="Digite o nome do produto"
                            className="border border-primary"
                            ref={refTermo}
                        />
                    </Col>
                    {pesquisarPor === "NOME" && categorias.length > 0 &&
                        <Col md="4" xl="auto">
                            <Form.Label>Categoria:</Form.Label>
                            <Form.Select
                                value={categoria}
                                onChange={(e) => setCategoria(e.target.value)}
                            >
                                {categorias.map((categ, index) => (
                                    <option key={index} value={categ.id}>{categ.desc}</option>
                                ))}
                            </Form.Select>
                        </Col>
                    }
                </Row>
                <Row className="align-items-end justify-content-center">
                    <Col md="3" xl="2">
                        <Form.Label>Período:</Form.Label>
                        <Form.Select
                            value={periodo}
                            onChange={(e) => setPeriodo(e.target.value)}
                        >
                            <option value="6">Última hora</option>
                            <option value="5">Últimas 6 horas</option>
                            <option value="4">Últimas 12 horas</option>
                            <option value="3">Últimas 24 horas</option>
                            <option value="2">Últimos 7 dias</option>
                            <option value="1">Últimos 15 dias</option>
                            <option value="-1">Últimos 2 meses</option>
                        </Form.Select>
                    </Col>
                    <Col md="3" xl="2">
                        <Form.Label>Ordenação:</Form.Label>
                        <Form.Select
                            value={ordem}
                            onChange={(e) => setOrdem(e.target.value)}
                        >
                            <option value="0">Menor preço</option>
                            <option value="1">Distância</option>
                            <option value="2">Data da venda</option>
                            <option value="3">Maior preço</option>
                        </Form.Select>
                    </Col>
                    <Col md="3" xl="2">
                        <Form.Label>Distância: {distancia} km</Form.Label>
                        <Form.Range min="1" max="20" step="1" value={distancia} onChange={(e) => setDistancia(e.target.value)} />
                    </Col>
                    <Col md="3" xl="1">
                        <Button onClick={pesquisar} className="w-100">Buscar</Button>
                    </Col>
                </Row>
            </div>
            {/* .Filtros */}

            {/* Lista de produtos */}
            <div className="my-4">
                {loading ? (
                    <div className="text-center">
                        <Spinner animation="border" variant="secondary" />
                    </div>
                ) : produtos.length > 0 ? (
                    <>
                        {produtos.map((produto, index) => (
                            <CardProduto key={index} idx={index + 1} produto={produto} pesquisarPorCodigo={pesquisarPorCodigo} />
                        ))}
                    </>
                ) : (
                    <p className="text-center text-secondary">Nenhum produto encontrado</p>
                )}
            </div>
            {/* .Lista de produtos */}

        </Container>
        {/* .Meio */}

        <ModalEndereco
            visivel={modalVisivel}
            fecharModal={() => setModalVisivel(false)}
            selecionouEndereco={selecionouEndereco}
            podeFechar={!!endereco}
        />

        <ModalHistorico
            visivel={modalHistoricoVisivel}
            fecharModal={() => setModalHistoricoVisivel(false)}
            historicos={historicos}
            pesquisarHistorico={pesquisarHistorico}
        />
    </>);
}

export default App;
