import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";
import ListGroup from "react-bootstrap/ListGroup";
import Modal from "react-bootstrap/Modal";
import { format } from 'date-fns';

const ModalHistorico = ({ visivel, fecharModal, historicos, pesquisarHistorico }) => {

    return (
        <Modal
            show={visivel}
            onHide={fecharModal}
            scrollable
        >
            <Modal.Header closeButton>
                <Modal.Title>Histórico de pesquisa</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {historicos.length === 0 &&
                    <Alert variant="warning">
                        Nenhum produto pesquisado.
                    </Alert>
                }
                {historicos.length > 0 &&
                    <ListGroup>
                        {historicos.map((historico, index) =>
                            <ListGroup.Item key={index} action onClick={() => pesquisarHistorico(historico)}>
                                <div>
                                    <span className="fw-bold">{historico.tipo === "CODIGO" ? "Código" : "Nome"}: </span>
                                    {historico.termo}
                                </div>
                                <span className="form-text text-secondary">
                                    {format(new Date(historico.data), 'dd/MM/yyyy HH:mm')}
                                </span>
                            </ListGroup.Item>
                        )}
                    </ListGroup>
                }
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={() => fecharModal()}>Fechar</Button>
            </Modal.Footer>
        </Modal>
    );
};

export default ModalHistorico;
