import { Geocoder } from '@mapbox/search-js-react';
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";

const MAPBOX_ACCESS_TOKEN = process.env.REACT_APP_MAPBOX_ACCESS_TOKEN;

const ModalEndereco = ({ visivel, fecharModal, selecionouEndereco, podeFechar }) => {

    return (
        <Modal
            show={visivel}
            onHide={fecharModal}
            backdrop="static"
            keyboard={false}
        >
            <Modal.Header>
                <Modal.Title>Qual o seu endereço?</Modal.Title>
            </Modal.Header>
            <Modal.Body>

                <Alert variant="primary" className="mb-3">
                    Insira um endereço completo, incluindo rua e número, para obter resultados mais precisos.
                </Alert>

                <Geocoder
                    accessToken={MAPBOX_ACCESS_TOKEN}
                    options={{
                        language: 'pt',
                        country: 'BR'
                    }}
                    onRetrieve={(result) => selecionouEndereco({
                        nome: result.properties.name,
                        latitude: result.properties.coordinates.latitude,
                        longitude: result.properties.coordinates.longitude
                    })}
                />

            </Modal.Body>
            {podeFechar &&
                <Modal.Footer>
                    <Button variant="danger" onClick={() => fecharModal()}>Cancelar</Button>
                </Modal.Footer>
            }
        </Modal>
    );
};

export default ModalEndereco;
