import { format } from 'date-fns';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import './CardProduto.css';

const CardProduto = ({ idx, produto, pesquisarPorCodigo }) => {
    const dataHoraFormatada = format(new Date(produto.datahora), 'dd/MM/yyyy HH:mm');

    return (
        <Card className={`mb-3 card-destaque ${idx % 2 !== 0 ? 'bg-body-secondary' : ''}`}>
            <Card.Body>
                <Card.Title>
                    {`#${idx} - ${produto.desc}`}
                </Card.Title>
                <Card.Subtitle className="mb-2 text-body-secondary">
                    {produto.tempo}
                    <span className="fw-light" style={{ marginLeft: 10, fontSize: 12 }}>
                        ({dataHoraFormatada})
                    </span>
                </Card.Subtitle>
                <Row>
                    <Col>
                        <span className="fw-bold">Valor: </span>
                        R$ {produto.valor}
                        <span className="fw-light" style={{ marginLeft: 10, fontSize: 12 }}>
                            {`(${produto.valor_tabela} - ${produto.valor_desconto})`}
                        </span>
                    </Col>
                    <Col>
                        <span className="fw-bold">Distância: </span>
                        {produto.distkm} km
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <span className="fw-bold">Estabelecimento: </span>
                        {`${produto.estabelecimento.nm_fan}${produto.estabelecimento.nm_fan && produto.estabelecimento.nm_emp ? ' - ' : ''}${produto.estabelecimento.nm_emp}`}
                    </Col>
                    <Col>
                        <span className="fw-bold">Endereço: </span>
                        {`${produto.estabelecimento.nr_logr}, ${produto.estabelecimento.tp_logr} ${produto.estabelecimento.nm_logr}, ${produto.estabelecimento.bairro}${produto.estabelecimento.complemento ? ' - ' + produto.estabelecimento.complemento : ''}`}
                    </Col>
                </Row>
            </Card.Body>
            <Card.Footer className="bg-transparent">
                <Button variant="secondary" href={`https://www.google.com/maps/search/?api=1&query=${produto.estabelecimento.nr_logr}, ${produto.estabelecimento.tp_logr} ${produto.estabelecimento.nm_logr}`} target="_blank" rel="noreferrer">
                    Mapa
                </Button>
                <Button variant="secondary" className="ms-3" onClick={() => pesquisarPorCodigo(produto.gtin)}>
                    Pesquisar por código
                </Button>
            </Card.Footer>
        </Card>
    );
}

export default CardProduto;